#home_container .card {
    width: 18rem;
    margin: 2rem;
    padding: .75rem;
    background-color: #bdc6bc;;
    box-shadow: 3px 3px 5px  white, 5px 5px 20px black;
}



#home_container button {
    width: 100%;
    background: #aa2222;
    box-shadow: 2px 2px black;
    font-family: nosifer, fantasy;
    font-size: 1.5vw;
    color: white;
    text-shadow: 3px 3px 0 black;
}

#home_container button:hover {
    box-shadow: 0px 0px black;
    text-shadow: 2px 2px 0 black;
}



#card_container {
    height: 80vh;
    overflow: auto;
}

@media screen and (max-width: 425px) {
    #home_container footer {
        margin-top: -20vh; 
     }
     #home_container button {
        font-size: 5vw;
    }

}