nav {
  /* background-color: #342626; */
  height: 15vh;
}

nav,
nav a {
  color: #bdc6bc;
  text-shadow: 2px 2px 0 black, 3px 3px 0 #aa2222;
  font-family: nosifer, fantasy;
}

.navbar-brand img {
  height: 10vh;
}

button[type="button"] {
  border: black 3px solid;
  border-radius: 50%;
}

nav a:hover {
  cursor: pointer;
  color: #aa2222;
  text-shadow: 2px 2px 0 black, 3px 3px 0 #bdc6bc;
  text-decoration: none;
}
